<template>
  <div class="rating_page">
    <head-top head-title="会员中心" go-back='true'></head-top>
    <section v-if="userInfo">
      <p class="buy_for">为账户 <span>{{userInfo.username}}</span> 购买会员</p>
      <section class="vip_prerogative">
        <router-link to="/vipcard/vipDescription" class="header_style">
          <span class="header_left">会员特权</span>
          <section class="header_right">
            <span>会员说明</span>
            <span class="arrow-right"></span>
          </section>
        </router-link>
        <section class="vip_detail">
          <div class="vip_detail_left">
            <img src="../../../static/images/common/sheng.jpeg" alt="sheng">
          </div>
          <div class="vip_detail_right">
            <h4>减免配送费</h4>
            <p>每月减免30单，每日可减免3单，每单最高减4元</p>
            <p>蜂鸟专送专享</p>
          </div>
        </section>
        <section class="vip_detail">
          <div class="vip_detail_left">
            <img src="../../../static/images/common/jifen.jpeg" alt="jifen">
          </div>
          <div class="vip_detail_right">
            <h4>减免配送费</h4>
            <p>每月减免30单，每日可减免3单，每单最高减4元</p>
            <p>蜂鸟专送专享</p>
          </div>
        </section>
      </section>
      <section class="apply_vip">
        <header class="header_style">
          <span class="header_left">开通会员</span>
        </header>
        <section class="apply_vip_buy">
          <div class="apply_vip_buy_left">
            <span>1个月</span>
            <span> ¥20</span>
          </div>
          <div class="apply_vip_buy_right" @click="buyCart">购买</div>
        </section>
      </section>
      <router-link to="/vipcard/useCart" class="header_style common_style">
        <span class="header_left">兑换会员</span>
        <section class="header_right">
          <span>使用卡号卡密</span>
          <span class="arrow-right"></span>
        </section>
      </router-link>
      <router-link to="/vipcard/invoiceRecord" class="header_style common_style">
        <span class="header_left">购买记录</span>
        <section class="header_right">
          <span>开发票</span>
          <span class="arrow-right"></span>
        </section>
      </router-link>
    </section>
    <transition name="router-slid" mode="out-in">
      <router-view></router-view>
    </transition>
  </div>
</template>

<script>
  import headTop from '@/components/header/head';
  import {mapState, mapMutations} from 'vuex';

  export default {
    data(){
      return{

      }
    },
    mounted(){

    },
    computed: {
      ...mapState([
        'userInfo',
      ]),
    },
    components: {
      headTop
    },
    methods: {
      ...mapMutations([
        'ORDER_SUCCESS', 'BUY_CART'
      ]),
      buyCart(){
        this.ORDER_SUCCESS({order_id: '399525134200981325'});
        this.BUY_CART(20);
        this.$router.push('/confirmOrder/payment');
      },
    }
  }
</script>

<style lang="scss" scoped>
  @import '@/../static/style/mixin';

  .arrow-right {
    display: inline-block;
    width: 12px;
    height: 12px;
    @include bis('/../../static/images/common/arrow-rt-999.svg');
  }
  .rating_page{
    padding-top: 46px;
    p, span{
      font-family: Helvetica Neue,Tahoma,Arial;
    }
  }
  .buy_for{
    @include sc(14px, #666);
    line-height: 46px;
    padding-left: 16px;
    span{
      font-weight: bold;
    }
  }
  .vip_prerogative{
    background-color: #fff;
    padding-left: 16px;
    margin-bottom: 12px;
    .vip_detail{
      display: flex;
      border-bottom: 1px solid #f5f5f5;
      padding: 18px 0;
      .vip_detail_left{
        margin-right: 14px;
        img{
          @include wh(38px, 48px);
        }
      }
      .vip_detail_right{
        h4{
          @include sc(16px, #333);
          font-weight: normal;
        }
        p{
          @include sc(12px, #999);
        }
      }
    }
  }
  .apply_vip{
    background-color: #fff;
    padding-left: 16px;
    margin-bottom: 12px;
    .apply_vip_buy{
      @include fj;
      align-items: center;
      padding-right: 16px;
      font-size: 16px;
      line-height: 62px;
      .apply_vip_buy_left{
        span:nth-of-type(2){
          font-weight: bold;
          color: #f60;
        }
      }
      .apply_vip_buy_right{
        border: 1px solid #f60;
        border-radius: 5px;
        line-height: 28px;
        height: 28px;
        width: 62px;
        text-align: center;
        @include sc(14px, #f60);
      }
    }
  }
  .header_style{
    @include fj;
    background-color: #fff;
    line-height: 46px;
    border-bottom: 1px solid #f5f5f5;
    padding-right: 16px;
    .header_left{
      @include sc(16px, #333);
    }
    .header_right{
      display: flex;
      align-items: center;
      span{
        @include sc(14px, #999);
        margin-right: 5px;
      }
      svg{
        @include wh(12px, 12px);
      }
    }
  }
  .common_style{
    padding: 0 16px;
    margin-bottom: 12px;
  }
  .router-slid-enter-active, .router-slid-leave-active {
    transition: all .4s;
  }
  .router-slid-enter, .router-slid-leave-active {
    transform: translate3d(46px, 0, 0);
    opacity: 0;
  }
</style>



